@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Exo+2:wght@400;500;600&display=swap");

.Group {
  width: 493px;
  height: 352px;
}

.Ellipse {
  width: 108px;
  height: 108px;
  margin: 0 385px 40px 0;
  object-fit: contain;
}

.Name {
  width: 493px;
  height: 36px;
  margin: 40px 0 24px;
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: -0.36px;
  text-align: left;
  color: #000;
}

.Description {
  width: 493px;
  height: 90px;
  margin: 24px 0;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: -0.27px;
  text-align: left;
  color: rgba(0, 0, 0, 0.8);
}

.LearnMore {
  width: 493px;
  height: 30px;
  margin: 24px 0 0;
  font-family: "Exo 2", sans-serif;
  font-size: 18px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: -0.27px;
  text-align: left;
  color: #18a0fb;

  cursor: pointer;
}
