@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap");

.Rectangle6 {
  width: 400px;
  height: 415px;
  padding: 15px 20px 23px;
  border-radius: 10px;
  background-color: rgba(217, 217, 217, 0.5);
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

.TitleContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.CountryAndSights {
  width: 79px;
  height: 20px;
  margin: 0 243px 9px 3px;

  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.24px;
  text-align: left;
  color: #000;

  white-space: nowrap;
  
}

.Year {
  width: 30px;
  height: 20px;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.24px;
  text-align: right;
  color: #000;
}

.Image {
  width: 360px;
  height: 227.1px;
  margin: 9px 0 19.9px;
  object-fit: contain;
  border-radius: 5px;
}

.Description {
  width: 357px;
  height: 101px;
  margin: 0 0 0 3px;

  overflow: auto;

  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.21px;
  text-align: left;
  color: #000;
}

.Description::-webkit-scrollbar {
  /*스크롤바 제거*/
  width: 0;
  height: 0;
}

