@import url("https://fonts.googleapis.com/css2?family=Exo+2:wght@400;500;600&display=swap");
.Rectangle4 {
  width: 1680px;
  height: 300px;
  flex-grow: 0;
  margin: 0 0 1px;
  padding: 120px 0 0 80px;
  background-color: #fff;

  box-sizing: border-box;
}

.DuisTinciduntUtLigulaVitaeMollis {
  display: block;

  width: 750px;
  height: 71px;
  flex-grow: 0;
  font-family: "Exo 2", sans-serif;
  font-size: 48px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: -0.72px;
  text-align: left;
  color: #000;

  margin-bottom: 50px;
}

.FrameContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;

  margin-bottom: 68px;
}

.Frame1 {
  width: 408px;
  height: 50px;
  flex-grow: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 5px;
  margin: 0 20px 0 0;
  padding: 5px;
  border-radius: 25px;
  border: solid 1px rgba(0, 0, 0, 0.5);
  background-color: #fff;
}

.Frame2 {
  width: 408px;
  height: 50px;
  flex-grow: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0 764px 0 20px;
  padding: 5px;
  border-radius: 25px;
  border: solid 1px rgba(0, 0, 0, 0.5);
  background-color: #fff;
}

.YearNumber {
  /* margin: 0 76px 0 0; */
  color: #fff;
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%;
  background-color: #999;
  flex-grow: 0;
  font-family: "Exo 2", sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: -0.24px;
  text-align: center;
  cursor: pointer;
}

.Frame2 .YearNumber:last-child {
  margin-right: 0px;
}

.SelectedNum {
  background-color: black;
}

/*막대기*/
.YearBar {
  width: 68px;
  height: 5px;
  background-color: #ccc;
  transition: background-color 0.3s;
}

/*선택된막대기*/
.SelectedBar {
  background-color: black;
}

.Frame1 .CountryName:nth-child(1) {
  width: 64px;
}
.Frame1 .CountryName:nth-child(2) {
  width: 64px;
}
.Frame1 .CountryName:nth-child(3) {
  width: 81px;
}
.Frame1 .CountryName:nth-child(4) {
  width: 81px;
}
.Frame1 .CountryName:nth-child(5) {
  width: 88px;
}

.Selected {
  background-color: black;
  border-radius: 19px;
  color: #fff;
  transition: background-color 0.3s;
}

.CountryName {
  height: 40px;
  line-height: 40px;
  flex-grow: 0;
  font-family: "Exo 2", sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: -0.24px;
  text-align: center;
  cursor: pointer;
}

.CardContainer {
  width: 1600px;

  display: flex;
  flex-direction: row;
  gap: 40px;

  overflow-x: auto;

  padding-bottom: 5px;
}

/* 스크롤바 디자인 */
.CardContainer::-webkit-scrollbar {
  height: 6px;
}

.CardContainer::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

.CardContainer::-webkit-scrollbar-thumb {
  background: #a5a5a5;
  border-radius: 10px;
}

.CardContainer::-webkit-scrollbar-thumb:hover {
  background: #777777;
}
